/*Scroll */
.scrollTop {
	position: fixed;
	bottom: 60px;
	right: 20px;
	width: 35px;
	height: 35px;
	z-index: 99;
	text-align: center;
	line-height: 35px;
	cursor: pointer;
	display: none;
	border: 2px solid #ffffff;
	background-color: #333b61;
	border-radius: 5px;
}
.scrollTop:hover {
	background: #333b61;
}
.scrollTop:before {
	content: "";
	border-top: 3px solid #fff;
	border-right: 3px solid #fff;
	display: inline-block;
	margin: auto;
	width: 10px;
	height: 10px;
	transform: rotate(-45deg);
	vertical-align: middle;
}
.scrollTop:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: -1;
}
@media (max-width: 575.98px) {
	.scrollTop {
	bottom: 30px;
	width: 30px;
	height: 30px;
	line-height: 30px;
	}
}

